@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  padding: 10px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

#navbar .left {
  flex: 1 1;
}

#navbar .middle {
  flex: 1 1;
}

#navbar .logout-button {
  flex: 1 1;
  display: block;
  border: none;
  background-color: transparent;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: 500;
  text-align: right;
  cursor: pointer;
}

#navbar .logout-button:hover {
  color: darkgray;
}

#navbar a {
  font-size: 18px;
  font-weight: 900;
  color: #000;
  text-decoration: none;
  margin: 0 10px;
}

#navbar a:hover {
  color: darkgray;
}

#navbar .active {
  color: #1989F1;
}
#login-page {
  height: 100vh;
  width: 100vw;
  /* Center vertically and horizontally */
  display: flex;
  justify-content: center;
  align-items: center;
}

#login-page .card {
  padding: 20px 50px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.25);
}

#login-page .title {
  font-size: 32px;
  font-weight: 900;
  margin: 10px;
}

#login-page .sub-title {
  font-size: 16px;
  font-weight: 200;
  margin: 20px;
}

#login-page .error-message {
  font-size: 14px;
  color: #f00;
  margin: 5px 0 10px 0;
}

#login-page input {
  font-family: 'Montserrat', sans-serif;
  margin-top: 10px;
  width: 100%;
  padding: 10px;
}

#login-page button {
  font-family: 'Montserrat', sans-serif;
  color: #FFF;
  font-size: 20px;
  padding: 10px;
  background: #1989F1;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  width: 100%;
}
#users-page {
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#users-page .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#users-page .search-input {
  min-width: 20vw;
  margin-right: 20px;
  padding: 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  border: none;
  box-shadow: 4px 4px 10px 4px rgba(0, 0, 0, 0.25);
  outline: none;
}

#users-page .add-account-button {
  padding: 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  color: #FFF;
  border: none;
  background-color: #1989F1;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}

#users-page .user-stats {
  font-size: 16px;
}

#users-page .count {
  color: #1989F1;
  font-weight: 900;
}

#users-page table {
  margin-top: 20px;
  border-collapse: collapse;
  border: 1px solid #C7C4C4;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

#users-page thead th {
  padding: 15px 0;
}

#users-page tbody td {
  padding: 5px 0;
}

#users-page tr {
  border: 1px solid #C7C4C4;
}

#users-page button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

#users-page .edit-button img {
  height: 19px;
  width: 19px;
}

#users-page .delete-button img {
  height: 25px;
  width: 25px;
}

.app {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}
